* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

:root {
    --primary: #80CBC4;
    --dark: #263238;
    --dark-light: #3C474C;
    --light: #8B969A;
    --bg-dark: rgba(0,0,0,.75);
    --spacing-one: calc(1rem * 0.5);
    --spacing-two: calc(1rem);
    --spacing-five: calc(2.5rem);
    --main-font: 'Ubuntu Condensed', 'Roboto', sans-serif;
}

body {
    font-family: var(--main-font);
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background: url(./assets/images/background.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: bottom;
    background-attachment: fixed;
    height: 100vh;
    color: white;
    overflow-x: hidden;
}

main {
    top: 7.5rem;
    position: absolute;
}

/* display styles */
.h100 {
    height:100%
}

.w25 {
    width: 25%;
}

.w75 {
    width: 75%;
}

.w100 {
    width: 100%
}

.vw100 {
    width: 100vw;
}

.flex-row {
    display: flex;
    flex-wrap: wrap;
}

.justify-space-between {
    justify-content: space-between;
}

.justify-center {
    justify-content: center;
}

.align-center {
    align-content: center;
}

.circle {
    border-radius: 100%;
}

.bg-dark {
    background-color: var(--bg-dark);
}

/* margin and padding styles */
.mx-1 {
    margin-right: var(--spacing-one);
    margin-left: var(--spacing-one);
}
  
.mx-2 {
    margin-right: var(--spacing-two);
    margin-left: var(--spacing-two);
}

.mx-5 {
    margin-left: var(--spacing-five);
    margin-right: var(--spacing-five);
}
  
.my-1 {
    margin-top: var(--spacing-one);
    margin-bottom: var(--spacing-one);
}
  
.my-2 {
    margin-top: var(--spacing-two);
    margin-bottom: var(--spacing-two);
}
  
.my-5 {
    margin-top: var(--spacing-five);
    margin-bottom: var(--spacing-five);
}
  
.px-1 {
    padding-right: var(--spacing-one);
    padding-left: var(--spacing-one);
}
  
.px-2 {
    padding-right: var(--spacing-two);
    padding-left: var(--spacing-two);
}

.px-5 {
    padding-left: var(--spacing-five);
    padding-right: var(--spacing-five);
}
  
.py-1 {
    padding-top: var(--spacing-one);
    padding-bottom: var(--spacing-one);
}
  
.py-2 {
    padding-top: var(--spacing-two);
    padding-bottom: var(--spacing-two);
}

.py-5 {
    padding-top: var(--spacing-five);
    padding-bottom: var(--spacing-five);
}

.mt-1 {
    margin-top: var(--spacing-one);
}

.mt-2 {
    margin-top: var(--spacing-two);
}

.mr-1 {
    margin-right: var(--spacing-one);
}

.mb-1 {
    margin-bottom: var(--spacing-one);
}

.mb-2 {
    margin-bottom: var(--spacing-two);
}

.mb-5 {
    margin-bottom: var(--spacing-five);
}

.ml-2 {
    margin-left: var(--spacing-two);
}

.pl-2 {
    padding-left: var(--spacing-two);
}

.pb-5 {
    padding-bottom: var(--spacing-five);
}

/* text styles */
.text-primary {
    color: var(--primary);
}

.text-center {
    text-align: center;
}

.bold {
    font-weight: bold;
}

.italic {
    font-style: italic;
}

.text-small {
    font-size: .8rem;
}

.navActive {
    color: var(--primary);
    border-bottom: 1px solid var(--primary);
}

a, a:visited {
    font-family: var(--main-font);
    color: white;
    transition: color .5s;
}

a:hover {
    color: var(--primary)
}

/* container styles */
.section-container {
    padding: var(--spacing-five);
}

/* form control */
.form-control {
    margin-top: 5px;
    margin-bottom: 5px;
    padding: 5px;
    width: 100%;
    line-height: 1.5rem;
    font-family: var(--main-font);
    font-size: 1rem;
    border: 2px solid var(--primary);
    border-radius: 5px;
}

.form-control:focus {
    outline: none;
    border: 3px solid var(--primary)
}

.form-label {
    font-weight: bold;
    line-height: 1.5rem;
    margin-top: 2px;
    margin-bottom: 2px;
}

.btn {
    padding: .5rem .8rem;
    margin: .5rem 0;
    background-color: var(--dark-light);
    color: white;
    font-family: var(--main-font);
    font-weight: bold;
    border-radius: 5px;
    border: 1px solid var(--dark);
    transition: background-color .5s;
}

.btn:hover {
    background-color: var(--primary);
}


@media screen and (max-width: 576px) {
    .section-container {
        padding: var(--spacing-two);
    }
}